body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.movie-info {
  font-size: 20px;
  display: flex;
}
.info-section label {
  color: rgba(0, 0, 0, 0.5);
  margin: 5px;
}

.info-section span {
  font-weight: bold;
  margin: 5px;
}

.App {
  margin: 1em;
}

.search input {
  height: 30px;
  width: 60%;
  padding: 0 20px 0 25px;
  background: white;
  border: 1px solid #baccbd;
  border-radius: 20px;
  float: right;
}

.search input:focus {
  outline: none;
  border-color: #66ee83;
}

.search .results {
  display: block;
  padding: 0;
  margin: 0;
  width: 60%;
  float: right;
  border-width: 1px;
  border-style: solid;
  border-color: #cbcfe2 #c8cee7 #c4c7d7;
}

.search .results li {
  display: flex;
  align-items: center;
  padding: 5px;
  border-bottom: 1px solid rgba(88, 85, 85, 0.3);
  cursor: pointer;
}

.search .results li img {
  width: 30px;
  margin-right: 5px;
}

.search .results li:hover {
  background: rgba(88, 85, 85, 0.1);
}

